import React from 'react';
import { Link } from 'react-router-dom';
import './menubar.css';


const MenuBarItem = (props) => {

    const handleClick = () => {
        if (window.scrollY > 0) {
            window.scrollY = 0;
        }
    }

    return (
        <li style={{ listStyle: 'none', padding: 10 }}>
            <div style={{ padding: 2 }}>
                <Link onClick={handleClick} className="menuBarItem" to={props.to}>{props.itemValue}</Link>
            </div>
        </li>
    )
}

export default MenuBarItem;