import React from 'react';
import MobileBranding from '../Components/MobileBranding';
import WebBranding from '../Components/WebBranding';
import Media from 'react-media';
import './designs.css'
import brander from '../static/images/designs/BRANDER.jpg';
import metal4 from '../static/images/metal4.jpg';


const Designs = (props) => {

    return (
        <div>
            <div id="top-design-container">
                <h1 id="project-header">Recent Projects</h1>
                <div id="main-grid-container">
                    <div id="main-grid">
                        <div id="first-project" className="main-grid-item"></div>
                        <div id="second-project" className="main-grid-item"></div>
                        <div id="third-project" className="main-grid-item"></div>
                        <div id="fourth-project" className="main-grid-item"></div>
                    </div>
                </div>
            </div>
            <div style={{ marginTop: 500 }}>
                <h1 className="static-project">Introducing</h1>
            </div>
            <div id="second-design-container">
                <Media query="(max-width: 1090px)">
                    {matches => matches ? <MobileBranding brandingImage={brander} /> : <WebBranding brandingImage={brander} />}
                </Media>
            </div>
            <div style={{ marginTop: 100, textAlign: 'left' }}>
                <h1 className="static-project">Metal Frameworks</h1>
                <div className="grid-container" style={{ height: 750 }}>
                    <div id="frameworks-grid">
                        <div id="metal-first" className="main-grid-item-static"></div>
                        <div id="metal-second" className="main-grid-item-static"></div>
                        <div id="metal-third" className="main-grid-item-static"></div>
                        <div id="metal-fourth" className="main-grid-item-static"></div>
                    </div>
                </div>
            </div>
            <div style={{ marginTop: 100, textAlign: 'left' }}>
                <h1 className="static-project">Craftsmanship</h1>
                <div className="grid-container" style={{ height: 800 }}>
                    <div id="craftsmanship-grid">
                        <div id="craft-first" className="main-grid-item-static"></div>
                        <div id="craft-second" className="main-grid-item-static"></div>
                        <div id="craft-third" className="main-grid-item-static"></div>
                        <div id="craft-fourth" className="main-grid-item-static"></div>
                        <div id="craft-fifth" className="main-grid-item-static"></div>
                    </div>
                </div>
            </div>
            <div style={{ marginTop: 100, textAlign: 'left' }}>
                <h1 id="church-headers" className="static-project">Recycled Church Pews</h1>
                <div className="grid-container" style={{ height: 900 }}>
                    <div id="churchpew-grid">
                        <div id="church-first" className="main-grid-item-static"></div>
                        <div id="church-second" className="main-grid-item-static"></div>
                        <div id="church-third" className="main-grid-item-static"></div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Designs;