import React, { useState } from 'react';
import '../Pages/home.css';
import MessageCounter from './MessageCounter';
import MessageSuccess from '../Pages/MessageSuccess';
import emailjs from 'emailjs-com';

const ContactForm = (props) => {
    const [firstName, setFirstName] = useState("");
    const [lastName, setLastName] = useState("");
    const [phoneNumber, setPhoneNumber] = useState("");
    const [customerEmail, setCustomerEmail] = useState("");
    const [body, setBody] = useState("");
    const [messageSuccess, setMessageSuccess] = useState(false);
    const [error, setError] = useState("");

    const submitForm = (event) => {
        event.preventDefault();

        const customerName = `${firstName} ${lastName}`
        const templateParams = {
            Message: body,
            Email: customerEmail,
            Number: phoneNumber,
            Name: customerName
        }

        emailjs.send('production_service', '6to1', templateParams, 'user_mlIJr0OHx7aFpvjOuYlvF')
          .then(res => {
              if (res.status === 200) {
                setMessageSuccess(true);
              }
          })
          .catch(error => {
              setError(error.text);
          });
    }

    return (
        <div id="form-background">
            <div id="contact-form-container">
                {messageSuccess ? <MessageSuccess /> :
                    <form id="contact-form" onSubmit={event => submitForm(event)}>
                        <legend style={{ textAlign: 'left', display: 'table' }}>Contact<br />
                            <span style={{ color: 'red' }}>{error.length ? error : null}</span></legend>
                        <div className="table-row">
                            <div className="contact-form-table-cell">
                                <label>First Name:</label><br />
                                <input className="form-input" type="text" name="first" value={firstName} onChange={event => setFirstName(event.target.value)}></input>
                            </div>
                            <div className="contact-form-table-cell">
                                <label>Last Name:</label><br />
                                <input className="form-input" type="text" name="last" value={lastName} onChange={event => setLastName(event.target.value)}></input>
                            </div>
                        </div>
                        <div className="table-row">
                            <div className="contact-form-table-cell">
                                <label>Phone Number:</label><br />
                                <input className="form-input" type="text" name="number" value={phoneNumber} onChange={event => setPhoneNumber(event.target.value)}></input>
                            </div>
                            <div className="contact-form-table-cell">
                                <label>Email:</label><br />
                                <input className="form-input" type="text" name="email" value={customerEmail} onChange={event => setCustomerEmail(event.target.value)}></input><br />
                            </div>
                        </div>
                        <div className="table-row">
                            <div className="contact-form-table-cell">
                                <label>Message:</label><br />
                                <MessageCounter message={body} />
                                <textarea
                                    id="message-text-area"
                                    maxLength="200"
                                    className="form-input"
                                    name="message"
                                    value={body}
                                    onChange={event => setBody(event.target.value)}></textarea>
                            </div>
                            <div className="contact-form-table-cell">
                                <label></label><br />
                                <input id="contact-form-button" type="submit" value="Send Message"></input>
                            </div>
                        </div>
                    </form>}
            </div>
        </div>
    )
}

export default ContactForm;