import React, { useState } from 'react';
import emailjs from 'emailjs-com';
import MessageCounter from '../Components/MessageCounter';
import MessageSuccess from '../Pages/MessageSuccess';
import './home.css';


const Contact = (props) => {
    const [firstName, setFirstName] = useState("");
    const [lastName, setLastName] = useState("");
    const [number, setNumber] = useState("");
    const [email, setEmail] = useState("");
    const [message, setMessage] = useState("");
    const [formError, setFormError] = useState("");
    const [messageSuccess, setMessageSuccess] = useState(false);

    const validateForm = () => {
        if (firstName || lastName || number || email || message === null || undefined || '') {
            setFormError("Please fill in all form fields")
        }

        if (email.indexOf("@") < 1) {
            setFormError("Please enter a valid email")
        }
    }

    const submitForm = (props) => {
        validateForm();
        // Send message to Jason's email
        const templateParams = {
            Name: `${firstName} ${lastName}`,
            Message: message,
            Email: email,
            Number: number
        }
         
        //emailjs.send('jason_hotmail', '6to1', templateParams)
        emailjs.send('trey_hamilton21_gmail_com','template_ZYdWKszn', templateParams, 'user_mlIJr0OHx7aFpvjOuYlvF')
            .then(setMessageSuccess(true))
    }

    return (
        <div>
            <div id="form-background">
                <div>
                {messageSuccess ? <MessageSuccess /> : 
                <form id="contact-form">
                        <legend style={{ textAlign: 'center', display: 'table' }}>Contact<br />
                            <span style={{ fontSize: 12 }}>Send us a quick message.</span><br />
                            <span style={{color: 'red'}}>{formError}</span></legend>
                        <div className="table-row">
                            <div className="contact-form-table-cell">
                                <label>First Name</label><br />
                                <input className="form-input" type="text" name="first" value={firstName} onChange={event => setFirstName(event.target.value)}></input>
                            </div>
                            <div className="contact-form-table-cell">
                                <label>Last Name</label><br />
                                <input className="form-input" type="text" name="last" value={lastName} onChange={event => setLastName(event.target.value)}></input>
                            </div>
                        </div>
                        <div className="table-row">
                            <div className="contact-form-table-cell">
                                <label>Phone Number</label><br />
                                <input className="form-input" type="text" name="number" value={number} onChange={event => setNumber(event.target.value)}></input>
                            </div>
                            <div className="contact-form-table-cell">
                                <label>Email</label><br />
                                <input className="form-input" type="text" name="email" value={email} onChange={event => setEmail(event.target.value)}></input><br />
                            </div>
                        </div>
                        <div className="table-row">
                            <div className="contact-form-table-cell">
                                <label>Message</label><br />
                                <MessageCounter message={message} />
                                <textarea
                                    id="message-text-area"
                                    maxLength="200"
                                    className="form-input"
                                    name="message"
                                    value={message}
                                    onChange={event => setMessage(event.target.value)}></textarea>
                            </div>
                            <div className="contact-form-table-cell">
                                <label></label><br />
                                <input onClick={submitForm} id="contact-form-button" type="button" value="Send Message"></input>
                            </div>
                        </div>
                    </form>}
                </div>
            </div>
        </div>
    )
}

export default Contact;