import React from 'react';


const MobileAbout = (props) => {
    return (
        <figure style={{ maxWidth: 750, margin: 'auto' }}>
            <img src={props.imageSrc} id="third-container-image" alt="Wood Design" />
            <figcaption id="third-container-header">Behind the Name</figcaption>
            <figcaption className="third-container-content">{props.behindTheName[0]}</figcaption><br />
            <figcaption className="third-container-content">{props.behindTheName[1]}</figcaption><br />
            <figcaption id="bottom-text" style={{ backgroundColor: 'hsl(0, 0%, 90%)', borderRadius: 4, padding: 10 }} className="third-container-content">{props.behindTheName[2]}</figcaption>
        </figure>
    )
}

export default MobileAbout;