import React from 'react';


const WebAbout = (props) => {
    return (
        <p style={{ maxWidth: 1080, margin: 'auto', textAlign: 'left', textTransform: 'none', fontSize: 20 }}>
            <img style={{ float: 'left', marginRight: 20 }} src={props.imageSrc} id="third-container-image" alt="Wood Design" />
            <span style={{ textAlign: 'center' }} id="third-container-header">Behind the Name</span><br />
            <span>{props.behindTheName[0]}</span>
            <span>
                {props.behindTheName[1]}<br /><br />
                {props.behindTheName[2]}
            </span>
        </p>
    )
}

export default WebAbout;